

body {
  font-family: "Red Hat Display", sans-serif;
}

@keyframes slideUp {
  from {
    transform: translateY(30px);
    opacity: 0;
  }
  to {
    transform: translateY(0);
    opacity: 1;
  }
}

.card {
  opacity: 0;
  animation: slideUp 0.6s ease forwards;
}

.company-slider {
  position: relative;
  overflow: hidden;
}

.slide-content {
  position: relative;
  text-align: center;
  color: white;
}

.slide-image {
  width: 100%;
  height: 500px;
  object-fit: cover;
  filter: brightness(0.7);
}

.slide-text {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  text-shadow: 2px 2px 5px rgba(0, 0, 0, 0.7);
}


.nav-link {
  position: relative;
  text-decoration: none;
  color: #333;
  padding-bottom: 5px;
  transition: color 0.3s ease;
}

.nav-link:hover {
  color: #FFA500; /* Altere para a cor de hover desejada */
}

.nav-link-active {
  position: relative;
  color: #333;
  font-weight: bold;
}

.nav-link-active:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: -5px;
  width: 100%;
  height: 3px;
  background-color: #FFA500; /* Cor da linha embaixo */
  transition: width 0.3s ease;
}

.nav-link-active:hover {
  color: #FFA500; /* Altere para a cor de hover desejada */
}


/* Customização da velocidade de rotação */
@keyframes spin-slow {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}

.animate-spin-slow {
  animation: spin-slow 3s linear infinite; /* Rotação lenta (3 segundos) */
}
